import React from 'react';
import dynamic from 'next/dynamic';
import  BaseChat  from '../../components/chat/BaseChat.tsx';
const GitUrlImport = dynamic(
  () => import('../../components/git/GitUrlImport.client.tsx').then(mod => mod.default), 
  {
    ssr: false,
    loading: () => <BaseChat />,
  }
);

interface Props {
  id?: string;
}

export default function Home({ id }: Props) {
  return (
    <main>
      <GitUrlImport />
    </main>
  );
}
